
<template>
  <div id="app">
   <v-header :seller='seller'></v-header>
    <div class="tab border-1px">
    	<div class="tab-item">
          <router-link to='/goods'>商品</router-link>
        </div>
        <div class="tab-item">
            <router-link to='/ratings'>评论</router-link>
        </div>
        <div class="tab-item">
            <router-link to='/seller'>商家</router-link>
        </div>
    </div>
   <router-view :seller='seller'></router-view>
  </div>
</template>

<script>
/* export default 后面的对象 就相当于 new Vue() 构造函数中的接受的对象，它们都是定义组件所需要的数据（data）, 以及操作数据的方法等， 更为全面的一个 export default 对象，有methods, data, computed, 这时可以看到, 这个对象和new Vue() 构造函数中接受的对象是一模一样的。但要注意data 的书写方式不同。在 .vue 组件, data 必须是一个函数，它return（返回一个对象），这个返回的对象的数据，供组件实现。 */
    import sellheader from './components/header/header';

    const ERR_OK = 0;

    export default {
      data() {
        return {
          seller: {
            type: Object
          }
        };
      },
      created() { // 创建钩子函数，发送ajax请求
        this.$http.get('/api/seller').then((response) => {
          response = response.body;
          // console.log(response);
          if (response.errno === ERR_OK) {
            this.seller = response.data;
            console.log(this.seller);
          }
        });
      },
        components: {
           'v-header': sellheader
        }
    };
</script>

<style lang='less'> 
@import url("./common/css/same.less");
  #app .tab {
    display: flex;
    width: 100%;
    height: 40px;
    line-height: 40px;
    // border-bottom: 1px solid rgba(7,17,27,0.1);
    .border-1px(rgba(7,17,27,0.1));
  .tab-item {
    flex:1;text-align: center;
      & > a {
    display: block;
    font-size: 14px;
    color:rgb(77,85,93);
     &.active {
    color:rgb(240,20,20);
  }
  }
}
}
</style>



